@import "../../../assets/style/variables";
@import "src/assets/style/shablon";


.slider-image {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slider-title {
  position: absolute;
  text-align: center;
  z-index: 1;

  p {
    color: white;
    font-size: $font-size-20;
  }

  h3 {
    font-size: $font-size-48;
    color: white;
  }

  a {
    background-color: $red;
    font-size: 20px;
    padding: 15px 35px;
    border-radius: 10px;
    transition: .3s;
    color: white;

    &{
      @extend %hoverBgColorBlack

    }


  }
}

.slider-title > *:not(last-child) {
  margin: 10px;
}

.slick-dots {
  margin: 10px 0 50px 0;

  li {
    background-color: white;
    border-radius: 20px;
    margin: 0 10px;

    &.slick-active{
       background-color: $red;
    }

  }
}

